import indexPage from './pages/index'
import aboutPage from './pages/about'
import communityPage from './pages/community'
import lifePage from './pages/life'

export default {
  en: {
    type: '',
    button: {
      apply: 'APPLY NOW',
      more: 'Read More'
    },
    pages: {
      index: indexPage.en,
      about: aboutPage.en,
      community: communityPage.en,
      life: lifePage.en
    },
    menu: {
      title: 'MENU',
      list: [{
        title: 'Home',
        link: '/'}, {
        title: 'About ILUNO',
        link: '/about'}, {
        title: 'The Community',
        link: '/community'}, {
        title: 'Life in ILUNO',
        link: '/life'}, {
        title: 'Contact',
        link: '/contact'}]
    },
    footer: {
      apply: 'Come & join us today!'
    }
  },
  zh: {
    type: 'English',
    button: {
      apply: '',
      more: ''
    },
    pages: {
      index: indexPage.zh,
      about: aboutPage.zh,
      community: communityPage.zh,
      life: lifePage.zh
    },
    menu: {
      title: '',
      list: [{
        title: '',
        link: '/'}, {
        title: ' ILUNO',
        link: '/about'}, {
        title: '',
        link: '/community'}, {
        title: 'ILUNO',
        link: '/life'}, {
        title: '',
        link: '/contact'}]
    },
    footer: {
      apply: '!'
    }
  }
}



